import { info } from "autoprefixer";

export const format = (string) => {
    let acc = "";
    for (let i = 0; i < string.length; i++) {
      if (i === 0 || (string.charAt(i - 1) < 'a' || string.charAt(i - 1) > 'z')) {
        acc += string.charAt(i).toUpperCase();
      } else if (string.charAt(i) === "_") {
        acc += "-";
      } else {
        acc += string.charAt(i).toLowerCase();
      }
    }

    return acc;
  };


  export const termToString = (string) => {
    if(string === "short_term") {
      return "1 Month";
    }
    else if (string === "medium_term") {
      return "6 Months";
    }
    else {
      return "All-Time"
    }
  }